html::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1001;

    width: 15em;
    height: 8em;
    top: 0;
    left: 0;
    
    background: url('/assets/images/shapes.svg') no-repeat;
    background-size: cover;
    transform: rotate(0.5turn);
}

footer::after {
    content: "";
    display: block;
    position: absolute;
    z-index: -1001;

    width: 15em;
    height: 8em;
    bottom: 0;
    right: 0;
    
    background: url('/assets/images/shapes.svg') no-repeat;
    background-size: cover;
}

h1::after {
    content: "";
    display: block;
    position: absolute;
    z-index: -1000;

    width: 3.6em;
    height: 0.5em;
    margin-left: calc(50% - 1.8em);
    bottom: 0.05em;

    background-color: var(--color-3);
}

footer {
    height: 13em;
    section {
        margin-left: 5em;
        margin-right: 5em;
    }
}

.projects-list {
    list-style-type: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}

#design-projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 4em;
    column-gap: 4em;
}

#design-projects figure {
    margin: 0;
    position: relative;
}

#design-projects figcaption {
    color: var(--color-1);
    background-color: var(--color-2);
    padding: 1em 1.5em;
    font-weight: normal;

    position: absolute;
    bottom: -1em;
    left: -2em;

    text-align: left;
}

#design-projects img {
    max-width: 12em;
}

#design-projects {
    a:hover, a:focus {
        figcaption {
            background-color: var(--color-3);
        }
        img {
            transform: scale(1.025);
        }
    }
}

#translation-projects {
    line-height: 1.8;
    text-align: center;
}

#translation-projects li {
    display: inline;
    // padding-right: 0;
}

#translation-projects {
    li + li::before {
            display: inline-block;
            margin: 0.5em 0.5em 0 0.3em;
            width: 0.7em;
            height: 0.7em;
            border-radius: 50%;
            content: "";
            background-color: var(--color-3);
    }
}

#translation-projects a {
    text-decoration: none;
}

#translation-projects a:hover {
    text-decoration: underline;
}

#socials {
    font-family: var(--font-stack-accent);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-size: 0.7em;
    a {
        text-decoration: none;
    }

    ul {
        list-style: none;
        padding-left: 0;
    }
    li {
        display: inline-block;
    }
    li + li::before {
        display: inline-block;
        content: "·";
        margin: 0 0.5ch;
    }
}

@media (min-width: 600px) {
    footer {
        height: 8em;
    }
}
